
.App * {
  -webkit-user-select: none !important;
     -moz-user-select: none !important;
      -ms-user-select: none !important;
          user-select: none !important;  
}
.App row {
  position: static
}
.App li {
  list-style-type: none;
}
.App{
  padding: 0;
}

.App .PanelContainer{
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0%;
  bottom: 0%;
  left: 60%;
  top: 50%;
}

.AppPanel{
  border-radius: 10px;
  background: #999999;
  opacity: 0.8; 
  max-width: 40%;
  max-height: 50%;
  display: block;
}
.AppPanel:after {
  content: "";
  display: table; 
  clear: both;  
}


.AppPanel img{
  max-width: 50px;
}


body ul {
  padding-left: 0px;
}


